import React, { Component } from "react"; 

class DealForm extends Component {
  constructor(props){
    super()
    this.state = {
      comentDeal:"",
      ItemOne:"",
      price:""
    }
  }
  componentDidMount(props){
    console.log(props)
  }
  handleChangeRate = rate => {
    this.setState({ rate });
}
handleChange = name => event => {
    this.setState({ [name]: event.target.value }) 
}
  render() {
    const {DealId} = this.props
    console.log("ITS PROPS",DealId)
    const {comentDeal,ItemOne,price} = this.state
    return (
     <div>
       <h1>wqewqe</h1>
            <input type="textarea"onChange={this.handleChange("price")} value={price} />
           
<input onChange={this.handleChange("ItemOne")}  value={ItemOne} type="textarea" />


<input onChange={this.handleChange("comentDeal")}   value={comentDeal}  type="textarea" />


     </div>
    );
  }
}

export default DealForm

